import { CHANGE_PASSWORD_SUCCESS, TOGGLE_REFRESH_CHANGEPASSWORD } from "./constants";
import { changePassword } from "../../api";
import { showLoader, hideLoader } from "../../components/Loader/action";
import handleError from "../../errorHandler";
import { toast } from "react-toastify";

export function changePasswordSuccess(data) {
    return {
        type: CHANGE_PASSWORD_SUCCESS,
        data
    }
}

export function changePasswordAction(data) {
    return (dispatch) => {
        dispatch(showLoader());
        return changePassword(data)
            .then(response => {
                dispatch(hideLoader());
                toast.success(response.data.message);
                dispatch(changePasswordSuccess(response.data));
                dispatch(toggleRefreshPassword(true));
            })
            .catch(error => {
                handleError(error, dispatch);
            });
    };
}

export function toggleRefreshPassword(data) {
    return {
        type: TOGGLE_REFRESH_CHANGEPASSWORD,
        data
    }
}
