import React from 'react';
import { connect } from 'react-redux'
import { bindActionCreators } from 'redux';
import md5 from 'md5';
import { FormControl, InputLabel, Input, FormHelperText, Button } from '@material-ui/core';
import './style.scss';
import { changePasswordAction, toggleRefreshPassword } from './action';
import PageHeader from '../../components/PageHeader';
import { generateRandomNumber } from '../../utilities/generateRandomNumber';

class ChangePassword extends React.Component {
    state = {
        fields: {
            currentPassword: '',
            newPassword: '',
            confirmPassword: '',
            sum: ''
        },
        errors: {
            currentPassword: '',
            newPassword: '',
            confirmPassword: '',
            sum: ''
        },
        number1: null,
        number2: null,
        enableSubmit: false
    }

    componentDidMount() {
        this.setState({
            number1: generateRandomNumber(1, 9),
            number2: generateRandomNumber(1, 9)
        })
    }

    componentWillReceiveProps(props) {
        if (props.refreshChangePassword) {
            console.log("Reset...");
            this.handleReset();
            this.props.toggleRefreshPassword(false);
        }
    }

    validateForm = () => {
        let { fields } = this.state;
        let { errors } = this.state;
        let validFiedlCount = 0
        for (let key in fields) {
            if (fields[key] !== '' && errors[key] === '') {
                validFiedlCount++;
            }
        }
        this.setState({
            enableSubmit: validFiedlCount === Object.keys(errors).length
        });
    }
    handleChange = (event) => {
        let fieldName = event.target.name;
        let fieldValue = event.target.value;

        let fields = { ...this.state.fields };
        let errors = { ...this.state.errors };

        switch (fieldName) {
            case 'currentPassword': if (fieldValue.length > 0) {
                fields[fieldName] = fieldValue;
                errors[fieldName] = '';
            } else if (fieldValue.length === 0) {
                fields[fieldName] = fieldValue;
                errors[fieldName] = 'Current Password Should not be Empty';
            }
                break;

            case 'newPassword': if (fieldValue.length > 0) {
                fields[fieldName] = fieldValue;
                if (fields['confirmPassword'] !== '' && fields['newPassword'] !== fields['confirmPassword']) {
                    errors[fieldName] = 'New password and Confirm password didn\'t match';
                } else {
                    errors[fieldName] = '';
                    errors['confirmPassword'] = '';
                }
            } else if (fieldValue.length === 0) {
                fields[fieldName] = fieldValue;
                errors[fieldName] = 'New Password Should not be Empty';
            }
                break;

            case 'confirmPassword': if (fieldValue.length > 0) {
                fields[fieldName] = fieldValue;
                if (fields['newPassword'] !== '' && fields['newPassword'] !== fields['confirmPassword']) {
                    errors[fieldName] = 'New password and Confirm password didn\'t match';
                } else {
                    errors[fieldName] = '';
                    errors['newPassword'] = '';
                }
            } else if (fieldValue.length === 0) {
                fields[fieldName] = fieldValue;
                errors[fieldName] = 'Confirm Password Should not be Empty';
            }
                break;

            case 'sum': if (Number(fieldValue) > 0) {
                fields[fieldName] = /^[0-9]{0,2}$/.test(fieldValue) ? fieldValue : fields[fieldName];
                if (this.state.number1 + this.state.number2 === Number(fieldValue)) {
                    errors[fieldName] = '';
                } else {
                    errors[fieldName] = 'Invalid Sum';
                }
            } else if (!fieldValue) {
                fields[fieldName] = '';
                errors[fieldName] = 'Required';
            }
                break;

            default: fields[fieldName] = fieldValue;
        }

        this.setState({ fields, errors }, () => this.validateForm());
    }

    handleSubmit = () => {
        let fields = { ...this.state.fields };
        let currentPassword = md5(fields.currentPassword);
        let newPassword = md5(fields.newPassword);
        let data = { current_password: currentPassword, new_password: newPassword };
        this.props.changePasswordAction(data);
    }

    handleReset = () => {
        this.setState({
            fields: {
                currentPassword: '',
                newPassword: '',
                confirmPassword: '',
                sum: ''
            },
            errors: {
                currentPassword: '',
                newPassword: '',
                confirmPassword: '',
                sum: ''
            },
            number1: generateRandomNumber(1, 9),
            number2: generateRandomNumber(1, 9)
        });
    }
    changePasswordForm = () => {
        let { fields } = this.state;
        let { errors } = this.state;
        let title = "Change Password";
        return (
            <div className="change-password-container">
                <PageHeader
                    header={title}
                />
                <form className="change-password-form">
                    <FormControl className="full-width form-control-custom" error={errors.currentPassword}>
                        <InputLabel htmlFor="current-password">Current Password</InputLabel>
                        <Input
                            id="current-password"
                            name="currentPassword"
                            type="password"
                            value={fields.currentPassword}
                            onChange={this.handleChange}
                            aria-describedby="current-password-text"
                            fullWidth={true}
                            autoFocus={true}
                        />
                        <FormHelperText id="current-password-text">{errors.currentPassword}</FormHelperText>
                    </FormControl>
                    <br />

                    <FormControl className="full-width form-control-custom" error={errors.newPassword}>
                        <InputLabel htmlFor="new-password">New Password</InputLabel>
                        <Input
                            id="new-password"
                            name="newPassword"
                            type="password"
                            value={fields.newPassword}
                            onChange={this.handleChange}
                            aria-describedby="new-password-text"
                            fullWidth={true}
                        />
                        <FormHelperText id="new-password-text">{errors.newPassword}</FormHelperText>
                    </FormControl>
                    <br />

                    <FormControl className="full-width form-control-custom" error={errors.confirmPassword}>
                        <InputLabel htmlFor="confirm-password">Confirm Password</InputLabel>
                        <Input
                            id="confirm-password"
                            name="confirmPassword"
                            type="password"
                            value={fields.confirmPassword}
                            onChange={this.handleChange}
                            aria-describedby="confirm-password-text"
                            fullWidth={true}
                        />
                        <FormHelperText id="confirm-password-text">{errors.confirmPassword}</FormHelperText>
                    </FormControl>
                    <br />

                    <div className="number-container">
                        <FormControl className="full-width form-control-custom">
                            <InputLabel htmlFor="number-one"></InputLabel>
                            <Input
                                id="number-one"
                                // name="number1"
                                // type="number"
                                value={this.state.number1}
                                // onChange={this.handleChange}
                                aria-describedby="number-one-text"
                                fullWidth={true}
                                disabled={true}
                            />
                            <FormHelperText id="number-one-text"></FormHelperText>
                        </FormControl>

                        <div className="plus-symbol">+</div>

                        <FormControl className="full-width form-control-custom">
                            <InputLabel htmlFor="number-two"></InputLabel>
                            <Input
                                id="number-two"
                                // name="number2"
                                // type="number"
                                value={this.state.number2}
                                // onChange={this.handleChange}
                                aria-describedby="number-two-text"
                                fullWidth={true}
                                disabled={true}
                            />
                            <FormHelperText id="number-two-text"></FormHelperText>
                        </FormControl>

                        <div className="plus-symbol">=</div>

                        <FormControl className="full-width form-control-custom" error={errors.sum}>
                            <InputLabel htmlFor="sum">Result</InputLabel>
                            <Input
                                id="sum"
                                name="sum"
                                type="number"
                                value={fields.sum}
                                onChange={this.handleChange}
                                aria-describedby="sum-text"
                                fullWidth={true}
                            />
                            <FormHelperText id="sum-text">{errors.sum}</FormHelperText>
                        </FormControl>
                    </div>
                    <div className="button-container">
                        <Button variant="contained" color="primary" onClick={this.handleSubmit} disabled={!this.state.enableSubmit}>
                            Change
                        </Button>
                        <Button variant="contained" color="secondary" onClick={this.handleReset}>
                            Reset
                        </Button>
                    </div>
                </form>
            </div>
        );
    }
    render() {
        return this.changePasswordForm();
    }
}

const mapStateToProps = (state, ownProps) => {
    return {
        refreshChangePassword: state.changePassword.refreshChangePassword
    }
}

const mapDispatchToProps = (dispatch) => {
    return bindActionCreators({ changePasswordAction, toggleRefreshPassword }, dispatch)
}

export default connect(mapStateToProps, mapDispatchToProps)(ChangePassword);
